import noData from '../../components/hasNoData/noData'
import storage from '../../static/lib/storage'
export default {
  components: {
    noData
  },
  async asyncData (vm) {
    let status = vm.params.status
    let keywords = vm.query.keywords || ''
    let hotSearchList = []
    let categoryList = []
    let productList = []
    // 产品
    let productRes = await vm.$requests.searchProductContain({
      KEY_INFO: keywords,
      CUR_PAGE: 1
    })
    if (productRes.data.result === 'success') {
      productList = productRes.data.productList
    }
    // 类别
    let categoryRes = await vm.$requests.searchCategoryContain({
      KEY_INFO: keywords,
      CUR_PAGE: 1
    })
    if (categoryRes.data.result === 'success') {
      categoryList = categoryRes.data.categoryList
    }
    // 热门搜搜
    let res = await vm.$requests.indexSearchHot({
      CUR_PAGE: 1
    })
    if (res.data.result === 'success') {
      hotSearchList = res.data.hotsearchlogObj
    }
    return {
      status, // notFound/fuzzy
      keywords,
      hotSearchList,
      productList,
      categoryList,
      isPro: true,
      isCate: false,
      isSearch: false,
      searchList: [],
      statusMapper: {
        product: 'haveProductReviews',
        category: 'haveCategoryInformation',
        fuzzy: 'fuzzyMatching',
        notFound: 'allWithout'
      },
      timer: null,
      historySearch: []
    }
  },
  methods: {
    handleKeyWordTagClick2 (obj) {
      // 历史搜索本地存储
      let historySearch = storage.get('set_history_search')
      if (historySearch) {
        let index = historySearch.findIndex((item) => {
          return item.ID === obj.ID
        })
        if (index !== -1) {
          historySearch.splice(index, 1)
        }
        historySearch.unshift(obj)
        storage.set('set_history_search', historySearch)
      } else {
        let tmpArr = []
        tmpArr.unshift(obj)
        storage.set('set_history_search', tmpArr)
      }
      if (obj.TYPE === '1') {
        this.$router.push({
          name: 'productDetail-id',
          params: { id: obj.ID }
        })
      } else {
        this.$router.push({
          name: 'product-id',
          params: { id: obj.ID },
          query: { category_name: obj.NAME }
        })
      }
    },
    onSearch () {
      if (!this.keywords.trim()) {
        return false
      } else {
        this.$requests.indexKeysSearch({
          KEY_INFO: this.keywords.trim()
        }).then((res) => {
          if (res.status === 200 && res.data.result === 'success') {
            switch (res.data.searchStatus) {
              case this.statusMapper.category:
                this.$router.push({
                  name: 'product-id',
                  params: {
                    id: res.data.CATEGORY_ID
                  },
                  query: {
                    category_name: this.keywords.trim()
                  }
                })
                break
              case this.statusMapper.product:
                this.$router.push({
                  name: 'productDetail-id',
                  params: {
                    id: res.data.PRODUCT_ID
                  }
                })
                break
              case this.statusMapper.fuzzy:
                this.$router.push({
                  name: 'result-status',
                  params: {
                    status: 'fuzzy'
                  },
                  query: {
                    keywords: this.keywords.trim()
                  }
                })
                break
              case this.statusMapper.notFound:
                this.$router.push({
                  name: 'result-status',
                  params: {
                    status: 'notFound'
                  },
                  query: {
                    keywords: this.keywords.trim()
                  }
                })
                break
            }
          }
        })
      }
    },
    async onChange () {
      if (this.keywords.trim() === '') {
        this.isSearch = false
      } else {
        this.searchList = await this.searchAutoComplete()
        if (this.searchList) {
          this.isSearch = true
        } else {
          this.isSearch = false
        }
      }
    },
    onBlur () {
      this.timer = setTimeout(() => {
        this.isSearch = false
      }, 300)
    },
    async searchAutoComplete () {
      let resList = []
      let data = {
        KEY_INFO: this.keywords.trim()
      }
      let res = await this.$requests.indexSearchAutoComplete(data)
      if (res.status === 200 && res.data.result === 'success') {
        resList = res.data.productList
      }
      return resList
    },
    async handleKeyWordTagClick (obj) {
      let res = await this.$requests.indexKeysSearch({
        KEY_INFO: obj.names.trim()
      })
      if (res.status === 200 && res.data.result === 'success') {
        switch (res.data.searchStatus) {
          case this.statusMapper.category:
            this.$router.push({
              name: 'company-id',
              params: {
                id: res.data.CATEGORY_ID
              },
              query: {
                category_name: obj.value.trim()
              }
            })
            break
          case this.statusMapper.product:
            this.$router.push({
              name: 'productDetail-id',
              params: {
                id: res.data.PRODUCT_ID
              }
            })
            break
          case this.statusMapper.fuzzy:
            this.$router.push({
              name: 'result-status',
              params: {
                status: 'fuzzy'
              },
              query: {
                keywords: obj.names.trim()
              }
            })
            break
          case this.statusMapper.notFound:
            this.$router.push({
              name: 'result-status',
              params: {
                status: 'notFound'
              },
              query: {
                keywords: obj.names.trim()
              }
            })
            break
        }
      }
    },
    navigatoToPro (obj) {
      this.$router.push({
        name: 'productDetail-id',
        params: {
          id: obj.id
        }
      })
    },
    navigatoToCate (obj) {
      this.$router.push({
        name: 'company-id',
        params: {
          id: obj.id
        },
        query: {
          category_name: obj.name
        }
      })
    },
    async searchByProduct () {
      if (this.isPro) {
        return
      }
      this.isPro = true
      this.isCate = false
      let productRes = await this.$requests.searchProductContain({
        KEY_INFO: this.keywords,
        CUR_PAGE: 1
      })
      if (productRes.data.result === 'success') {
        this.productList = productRes.data.productList
      }
    },
    async searchByCategory () {
      if (this.isCate) {
        return
      }
      this.isPro = false
      this.isCate = true
      let categoryRes = await this.$requests.searchCategoryContain({
        KEY_INFO: this.keywords,
        CUR_PAGE: 1
      })
      if (categoryRes.data.result === 'success') {
        this.categoryList = categoryRes.data.categoryList
      }
    }
  }
}
